import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ReactiveFormsModule, FormGroup, FormControl, Validators } from '@angular/forms';
import { SharedService } from '../../shared/services/shared.service';
import { HfCardComponent, HfInputComponent, HfButtonComponent, HfCheckboxComponent, PhoneNumberFormatterDirective, inputMsgType, HfToastService } from '@monorepo/ui';
import { confirmPasswordValidator, maxLengthRestriction, noWhitespaceValidator } from '@monorepo/utils';
import { CookieHelper } from '../../shared/services/cookie.helper';
import { environment } from '../../../environments/environment';
import { AddressComponent } from '../../shared/components/address/address.component';

@Component({
  selector: 'app-user-account',
  standalone: true,
  imports: [ReactiveFormsModule, AddressComponent, HfCardComponent, HfInputComponent, HfButtonComponent, HfCheckboxComponent, PhoneNumberFormatterDirective],
  templateUrl: './user-account.component.html',
  styleUrl: './user-account.component.scss'
})
export class UserAccountComponent implements OnInit {

  userSettingData: any;

  userDetailsForm = new FormGroup({
    email: new FormControl('', [Validators.required, Validators.pattern(this.sharedService.emailKey)]),
    firstName: new FormControl('', [Validators.required, Validators.minLength(3), maxLengthRestriction(20)]),
    lastName: new FormControl('', [Validators.required, Validators.minLength(3), maxLengthRestriction(20)]),
    phoneNumber: new FormControl('', [Validators.required, maxLengthRestriction(14), Validators.pattern(this.sharedService.phoneNumberKey)]),
    backupPhoneNumber: new FormControl('', [maxLengthRestriction(14), Validators.pattern(this.sharedService.phoneNumberKey)]),
  });

  get email() {
    return this.userDetailsForm.get('email') as FormControl;
  }

  get firstName() {
    return this.userDetailsForm.get('firstName') as FormControl;
  }

  get lastName() {
    return this.userDetailsForm.get('lastName') as FormControl;
  }

  get phoneNumber() {
    return this.userDetailsForm.get('phoneNumber') as FormControl;
  }

  get backupPhoneNumber() {
    return this.userDetailsForm.get('backupPhoneNumber') as FormControl;
  }

  emailIM: string = 'Invalid Email Address';
  emailMT: inputMsgType = '';
  firstNameIM: string = 'Invalid First Name';
  firstNameMT: inputMsgType = '';
  lastNameIM: string = 'Invalid Last Name';
  lastNameMT: inputMsgType = '';
  phoneNumberIM: string = 'Invalid Phone Number';
  phoneNumberMT: inputMsgType = '';
  backupPhoneNumberIM: string = 'Invalid Backup Phone Number';
  backupPhoneNumberMT: inputMsgType = '';

  changePasswordForm = new FormGroup({
    oldPassword: new FormControl(null, [Validators.required, Validators.minLength(8), maxLengthRestriction(20), noWhitespaceValidator()]),
    newPassword: new FormControl(null, [Validators.required, Validators.minLength(8), maxLengthRestriction(20), noWhitespaceValidator()]),
    cfNewPassword: new FormControl(null, [Validators.required, Validators.minLength(8), maxLengthRestriction(20), noWhitespaceValidator()]),
  }, { validators: [confirmPasswordValidator] });

  get oldPassword() {
    return this.changePasswordForm.get('oldPassword') as FormControl
  }
  get newPassword() {
    return this.changePasswordForm.get('newPassword') as FormControl
  }
  get cfNewPassword() {
    return this.changePasswordForm.get('cfNewPassword') as FormControl
  }

  oldPasswordInvalidMsg: string = 'Invalid Old Password';
  oldPasswordMsgType: inputMsgType = '';
  newPasswordInvalidMsg: string = 'Invalid New Password';
  newPasswordMsgType: inputMsgType = '';
  cfNewPasswordInvalidMsg: string = 'Invalid Confirm New Password';
  cfNewPasswordMsgType: inputMsgType = '';
  passwordButtonText: string = 'CHANGE PASSWORD';
  isPasswordButtonDisabled: boolean = false;

  constructor(
    private sharedService: SharedService,
    private cookieHelper: CookieHelper,
    private http: HttpClient,
    private toastService: HfToastService
  ) { }

  ngOnInit() {
    this.callUserSettingApi();
  }

  callUserSettingApi() {
    const url = `${environment.api_url}customer/account/settings?userId=${this.cookieHelper.getUserID()}`;
    this.http.get(url).subscribe({
      next: (response) => {
        this.userSettingData = response;
        this.email.setValue(this.userSettingData['email']);
        this.firstName.setValue(this.userSettingData['firstName']);
        this.lastName.setValue(this.userSettingData['lastName']);
        this.phoneNumber.setValue(this.userSettingData['phoneNumber']);
        this.backupPhoneNumber.setValue(this.userSettingData['backupPhoneNumber']);
      },
      error: (error) => {
        console.log(error);
      }
    });
  }

  validateUserDetailsForm() {
    if (this.userDetailsForm.invalid) {
      if (this.email.invalid) {
        this.email.markAsTouched({ onlySelf: true });
        this.emailMT = 'error';
        if (this.email.errors?.['required']) {
          this.emailIM = 'Email is required';
        } else if (this.email.errors?.['pattern']) {
          this.emailIM = 'Invalid Email Address';
        }
        let vaildEmail = this.email.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.emailIM = '';
            this.emailMT = '';
            vaildEmail.unsubscribe();
          }
        });
      }
      if (this.firstName.invalid) {
        this.firstName.markAsTouched({ onlySelf: true });
        this.firstNameMT = 'error';
        if (this.firstName.errors?.['required']) {
          this.firstNameIM = 'First Name is required';
        } else if (this.firstName.errors?.['minlength']) {
          this.firstNameIM = 'First Name should be atleast 3 characters';
        }
        let vaildFirstName = this.firstName.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.firstNameIM = '';
            this.firstNameMT = '';
            vaildFirstName.unsubscribe();
          }
        });
      }
      if (this.lastName.invalid) {
        this.lastName.markAsTouched({ onlySelf: true });
        this.lastNameMT = 'error';
        if (this.lastName.errors?.['required']) {
          this.lastNameIM = 'First Name is required';
        } else if (this.lastName.errors?.['minlength']) {
          this.lastNameIM = 'Last Name should be atleast 3 characters';
        }
        let vaildLastName = this.lastName.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.lastNameIM = '';
            this.lastNameMT = '';
            vaildLastName.unsubscribe();
          }
        });
      }
      if (this.phoneNumber.invalid) {
        this.phoneNumber.markAsTouched({ onlySelf: true });
        this.phoneNumberMT = 'error';
        if (this.phoneNumber.errors?.['required']) {
          this.phoneNumberIM = 'Phone Number is required';
        } else if (this.phoneNumber.errors?.['pattern']) {
          this.phoneNumberIM = 'Invalid Phone Number';
        }
        let vaildphoneNumber = this.phoneNumber.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.phoneNumberMT = '';
            this.phoneNumberIM = '';
            vaildphoneNumber.unsubscribe();
          }
        });
      }
      if (this.backupPhoneNumber.invalid) {
        this.backupPhoneNumber.markAsTouched({ onlySelf: true });
        this.backupPhoneNumberMT = 'error';
        if (this.backupPhoneNumber.errors?.['pattern']) {
          this.backupPhoneNumberIM = 'Invalid Backup Phone Number';
        }
        let vaildbackupPhoneNumber = this.backupPhoneNumber.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.backupPhoneNumberMT = '';
            this.backupPhoneNumberIM = '';
            vaildbackupPhoneNumber.unsubscribe();
          }
        });
      }
      return;
    }
    this.saveUserSetting();
  }

  saveUserSetting() {
    this.userSettingData['email'] = this.email.value;
    this.userSettingData['firstName'] = this.firstName.value;
    this.userSettingData['lastName'] = this.lastName.value;
    this.userSettingData['phoneNumber'] = this.phoneNumber.value;
    this.userSettingData['backupPhoneNumber'] = this.backupPhoneNumber.value;

    let data = JSON.parse(JSON.stringify(this.userSettingData));

    const url = `${environment.api_url}customer/account/settings`;
    this.http.put(url, data).subscribe(
      {
        next: () => {
          this.toastService.toastMessage.next({ title: 'Success', msg: 'User Details Updated Successfully', type: 'success' });
        },
        error: (error) => {
          console.log(error);
        }
      }
    );
  }

  validateChangePasswordForm() {
    if (this.changePasswordForm.invalid) {
      if (this.oldPassword.invalid) {
        this.oldPassword.markAsTouched({ onlySelf: true });
        this.oldPasswordMsgType = 'error';
        if (this.oldPassword.errors?.['required']) {
          this.oldPasswordInvalidMsg = 'Old Password is required';
        } else if (this.oldPassword.errors?.['minlength']) {
          this.oldPasswordInvalidMsg = 'Old Password must be at least 8 characters';
        }
        let vaildOldPassword = this.oldPassword.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.oldPasswordInvalidMsg = '';
            this.oldPasswordMsgType = '';
            vaildOldPassword.unsubscribe();
          }
        });
      }
      if (this.newPassword.invalid) {
        this.newPassword.markAsTouched({ onlySelf: true });
        this.newPasswordMsgType = 'error';
        if (this.newPassword.errors?.['required']) {
          this.newPasswordInvalidMsg = 'New Password is required';
        } else if (this.oldPassword.errors?.['minlength']) {
          this.newPasswordInvalidMsg = 'New Password must be at least 8 characters';
        }
        let vaildNewPassword = this.newPassword.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.newPasswordInvalidMsg = '';
            this.newPasswordMsgType = '';
            vaildNewPassword.unsubscribe();
          }
        });
      }
      if (this.cfNewPassword.invalid) {
        this.cfNewPassword.markAsTouched({ onlySelf: true });
        this.cfNewPasswordMsgType = 'error';
        if (this.cfNewPassword.errors?.['required']) {
          this.cfNewPasswordInvalidMsg = 'Confirm New Password is required';
        } else if (this.cfNewPassword.errors?.['minlength']) {
          this.cfNewPasswordInvalidMsg = 'Confirm New Password must be at least 8 characters';
        }
        let vaildCfNewPassword = this.cfNewPassword.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.cfNewPasswordMsgType = '';
            this.cfNewPasswordInvalidMsg = '';
            vaildCfNewPassword.unsubscribe();
          }
        });
      }
      if (this.changePasswordForm.errors?.['PasswordNoMatch']) {
        this.cfNewPasswordMsgType = 'error';
        this.cfNewPasswordInvalidMsg = 'Password does not match';
        let matchNewPassword = this.changePasswordForm.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.cfNewPasswordMsgType = '';
            this.cfNewPasswordInvalidMsg = '';
            matchNewPassword.unsubscribe();
          }
        });
      }
      return;
    }
    this.changePassword();
  }

  changePassword() {
    const url = `${environment.api_url}account/changepassword`;
    const data = {
      id: this.cookieHelper.getUserID(),
      oldPassword: this.oldPassword.value,
      password: this.newPassword.value,
    };
    this.http.put(url, data).subscribe({
      next: () => {
        this.changePasswordForm.reset();
        this.toastService.toastMessage.next({ title: `Success`, msg: 'Password Changed', type: 'success' });
      },
      error: (error) => {
        if (error['error']['errors']['fieldName'] == 'OldPassword') {
          this.toastService.toastMessage.next({ title: `Invalid Password`, msg: `${error['errors']['errorMessage']}`, type: 'error' });
        }
      }
    })
  }
}
